import React from "react"
import {
  chakra,
  Flex,
  Button,
} from "@chakra-ui/react"

export const VendorSelections = (props) => {
  const fontWeight = "700"
  const fontFamily = "heading"
  const fontSize = {base: "14px", md: "24px"}
  const fontColor = "bastille"
  const borderColor = "bastille"
  const height = {base: "33px", md: "55px"}
  return (
    <Flex
      align={"center"}
      justify={"center"}
      direction={"row"}
      {...props}
    >
      <Button 
        bg={['mango']} 
        borderRadius={["0"]}
        textAlign={["center"]}
        border="1px"
        h={height}
        fontSize={fontSize}
        fontWeight={[fontWeight]}
        fontFamily={[fontFamily]}
        borderColor={[borderColor]}
        color={[fontColor]}
      >
        Vendors
      </Button>
      <Button 
        borderRadius={["0"]}
        textAlign={["center"]}
        borderTop="1px"
        borderBottom="1px"
        h={height}
        fontSize={fontSize}
        fontWeight={[fontWeight]}
        fontFamily={[fontFamily]}
        borderColor={[borderColor]}
        color={[fontColor]}
      >
        Business Data
      </Button>
      <Button 
        borderRadius={["0"]}
        textAlign={["center"]}
        borderTop="1px"
        borderBottom="1px"
        h={height}
        fontSize={fontSize}
        fontWeight={[fontWeight]}
        fontFamily={[fontFamily]}
        borderColor={[borderColor]}
        color={[fontColor]}
      >
        Advertising
      </Button>
      <Button 
        borderTop="1px"
        borderBottom="1px"
        borderRight="1px"
        h={height}
        borderRadius={["0"]}
        textAlign={["center"]}
        fontSize={fontSize}
        fontWeight={[fontWeight]}
        fontFamily={[fontFamily]}
        borderColor={[borderColor]}
        color={[fontColor]}
      >
       More
      </Button>
    </Flex>
  )
}

